@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,800;1,600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Do+Hyeon&family=Raleway:ital,wght@0,800;1,600&display=swap');

body, html {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  z-index: -2;
  background: #141414;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
